input[type="text"]::-webkit-calendar-picker-indicator {
  display: none;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  background: transparent;
}

input[type="date"]::-webkit-calendar-picker-indicator {
  margin-right: 14px;
}

select {
  -webkit-appearance: none;
  -moz-appearance: none;
}

#formTextArea {
  width: 502px;
}

#resize-mailInput {
  display: none;
}

#resize-phoneInput {
  display: none;
}

#resize-dateInput {
  display: none;
}

#filterInfo {
  display: none;
}

#resizeInnerContainer {
  display: none;
}

#logo {
  height: 29px;
  font-family: "Parkway Lush";
  background-color: #30323A;
  padding: 24px;
  border-radius: 12px;
  margin-bottom: -40px;
  display: none;
  margin-right: 1px;
}

html {
  scroll-behavior: smooth;
}

.body {
  display: flex;
  justify-content: center;
}

nav {
  width: 300px;
  height: 100vh;
  background: #30323A;
  position: sticky;
  top: 0;
}

nav .nav__logo {
  height: 29px;
  font-family: "Parkway Lush";
  color: #ffff;
  padding-top: 40px;
  padding-bottom: 40px;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

nav .megaphone {
  display: flex;
  margin-bottom: 20px;
  justify-content: flex-start;
}

nav .megaphone .megaphone-icon {
  width: 19px;
  height: 19px;
  color: #ffff;
  margin-right: 15px;
  margin-left: 30px;
}

nav .megaphone .megaphone-link {
  background-color: transparent;
  outline: none;
  border: none;
  color: #ffff;
  text-decoration: none;
  margin-right: 0px;
  font-size: 15px;
}

nav .megaphone .megaphone-link:hover {
  color: #be3144;
}

nav .tune {
