.main {
  width: 100%;
  background: #EFEFEF;
}

.main .resizeNav .resizeLinkBar .resizeInnerContainer .resize-megaphone .resize-megaphone-link {
  border: none;
  outline: none;
  background-color: #30323A;
  color: #FFFF;
}

.main .resizeNav .resizeLinkBar .resizeInnerContainer .resize-megaphone .resize-megaphone-link:hover {
  color: #8f1f2e;
}

.main .resizeNav .resizeLinkBar .resizeInnerContainer .resize-plus .resize-plus-link {
  border: none;
  outline: none;
  background-color: #30323A;
  color: #FFFF;
}

.main .resizeNav .resizeLinkBar .resizeInnerContainer .resize-plus .resize-plus-link:hover {
  color: #8f1f2e;
}

.main .main__login {
  display: flex;
  right: 0;
  float: right;
  align-items: center;
}

.main .main__login .loged-as {
  padding-right: 10px;
}

.main .main__login .log-in-out {
  width: 88px;
  height: 32px;
  background-color: #363840;
  color: #FFFFFFDE;
  margin-right: 10px;
}

.main .main__login .log-in-out:hover {
  background-color: #8f1f2e;
}

.main .main__description {
  padding-left: 20px;
  padding-top: 70px;
  margin-right: 10px;
}

.main .main__description .main__description__title {
  margin-bottom: 5px;
  font-size: 20px;
  color: #be3144;
}

.main .main__description .main__description__sub-title {
  margin-top: 0px;
}

.main .main__table {
  padding-top: 25px;
  margin-right: 10px;
}

.main .main__table .blocksHolder .table-form .submitBtn {
  background-color: #363840;
}

.main .main__table .blocksHolder .table-form .submitBtn:hover {
  background-color: #8f1f2e;
}

@media only screen and (min-width: 1700px) {
  body {
    width: 80vw;
    margin: auto;
  }
}

@media only screen and (max-width: 1000px) {
  nav {
    display: none;
  }
  #logo {
    display: inline-block;
  }
  #resizeInnerContainer {
    display: block;
  }
}

@media only screen and (max-width: 680px) {
  #resize-mailInput {
    display: block;
  }
  #resize-phoneInput {
    display: block;
  }
  #resize-dateInput {
    display: block;
  }
  #mailInputDiv {
    display: none;
  }
  #phoneInputDiv {
    display: none;
  }
  #dateInputDiv {
    display: none;
  }
  #formTextArea {
    width: 185px;
  }
  #addBlockFormDiv {
    margin-left: 18%;
  }
}

@media only screen and (max-width: 550px) {
  #blueDiv {
    padding-right: 4px;
  }
  #hiddenAreaEmailAndPhoneBlock {
    width: 100px;
  }
  #submitBtn {
    display: none;
  }
  #filterInfo {
    display: block;
  }
  #addBlockFormDiv {
    margin-left: 10%;
  }
}
